.nav__container {
	margin: 0 auto;
	text-align: center;
	position: sticky;
	top: 0;
	background: var(--color-bg-variant);
	padding: 1.2rem;
}

.nav__container a {
	font-size: 18px;
	color: var(--white);
}

.nav__container a:hover {
	color: var(--color-primary);
	text-decoration: underline;
	text-decoration-thickness: 3px;
}

.nav__container li {
	display: inline-block;
	margin: 0 1.2rem;
}

.nav__icon {
	display: none;
}

/* -------------- --------------- */
@media screen and (max-width: 600px){
	.nav__container {
		padding: .75rem;
	}
	
	.nav__icon {
		display: block;
	}
	.nav__text {
		display: none;
	}
	.nav__container a {
		font-size: 26px;
	}
	.nav__container li {
		margin-top: .75rem;
	}
}